import { api } from "api";

import { TOGGLE_LOADING, AUTH_ERROR } from "../types";

import { toast } from "react-toastify";

export const updateUser = (formValues, callback) => async dispatch => {
  dispatch({ type: TOGGLE_LOADING, payload: true });
  toast.info("User update is in progress");
  const token = localStorage.getItem("access_token");
  const user = await JSON.parse(localStorage.getItem("user"));
  formValues.userName = user.userName;
  try {
    const response = await api.post("/users/update/", formValues, {
      params: {
        access_token: token
      }
    });
    toast.success("User updated Successfully");
    dispatch({ type: TOGGLE_LOADING, payload: false });
    callback(response.data);
  } catch (e) {
    toast.error(e.response.data.error_description);
    dispatch({ type: AUTH_ERROR, payload: "User Update Failed" });
  }
};

