import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";

import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import DashboardIcon from "@material-ui/icons/Dashboard";
import AccountBalanceWalletIcon from "@material-ui/icons/AccountBalanceWallet";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import VerifiedUserIcon from "@material-ui/icons/VerifiedUser";
import SettingsIcon from "@material-ui/icons/Settings";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  },
  nested: {
    paddingLeft: theme.spacing(4)
  }
}));

const Sidebar = props => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [selectedIndex, setSelectedIndex] = React.useState(0);

  //Todo - when user navigate to nested link directly from browser url, then handle opening nested itm and select it

  const handleClick = () => {
    setOpen(!open);
  };
  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
  };
  function ListItemLink(props) {
    return <ListItem button component="a" {...props} />;
  }
  return (
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      className={classes.root}
    >
      <ListItem
        button
        selected={selectedIndex === 0}
        onClick={event => handleListItemClick(event, 0)}
      >
        <ListItemIcon>
          <DashboardIcon />
        </ListItemIcon>
        <ListItemText primary="Dashboard" />
      </ListItem>
      <ListItem
        button
        selected={selectedIndex === 1}
        onClick={event => handleListItemClick(event, 1)}
      >
        <ListItemIcon>
          <AccountBalanceWalletIcon />
        </ListItemIcon>
        <ListItemText primary="Wallet" />
      </ListItem>
      <ListItem
        button
        selected={selectedIndex === 2}
        onClick={event => handleListItemClick(event, 2)}
      >
        <ListItemIcon>
          <AttachMoneyIcon />
        </ListItemIcon>
        <ListItemText primary="Trade" />
      </ListItem>
      <ListItem
        button
        selected={selectedIndex === 3}
        onClick={event => handleListItemClick(event, 3)}
      >
        <ListItemIcon>
          <VerifiedUserIcon />
        </ListItemIcon>
        <ListItemText primary="Verification" />
      </ListItem>
      <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <SettingsIcon />
        </ListItemIcon>
        <ListItemText primary="Setting" />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" dense>
          <ListItemLink
            href="#profile/basic"
            button
            selected={selectedIndex === 4}
            onClick={event => handleListItemClick(event, 4)}
            className={classes.nested}
          >
            <ListItemText primary="Basic Profile" />
          </ListItemLink>
          <ListItemLink
            href="#profile/updatephone"
            button
            selected={selectedIndex === 5}
            onClick={event => handleListItemClick(event, 5)}
            className={classes.nested}
          >
            <ListItemText primary="Phone Update" />
          </ListItemLink>
          <ListItemLink
            href="#profile/updatepassword"
            button
            selected={selectedIndex === 6}
            onClick={event => handleListItemClick(event, 6)}
            className={classes.nested}
          >
            <ListItemText primary="Reset Password" />
          </ListItemLink>
          <ListItemLink
            href="#profile/updateemail"
            button
            selected={selectedIndex === 7}
            onClick={event => handleListItemClick(event, 7)}
            className={classes.nested}
          >
            <ListItemText primary="Change Email" />
          </ListItemLink>
          <ListItemLink
            href="#profile/updatepin"
            button
            selected={selectedIndex === 8}
            onClick={event => handleListItemClick(event, 8)}
            className={classes.nested}
          >
            <ListItemText primary="Reset Pin" />
          </ListItemLink>
        </List>
      </Collapse>
    </List>
  );
};

export default Sidebar;
